import React from "react";
import { Link } from "gatsby";
import { Box, Row, Col, Inline, InlineBlock } from "jsxstyle";
import menu from '../assets/menu.svg'
import close from '../assets/close.svg'

class DropdownMenu extends React.Component {
  constructor(props) {
    super(props);
    this.menu = props.mainMenu;
    this.path = props.path;
    this.state = {
      menuItem: null,
      dropdownOpen: false
    };
  }

  render() {

    return (
    <Box>
    {
        this.state.dropdownOpen && (
      <Col 
        paddingTop="36px"
        width="100%" 
        position="absolute" 
        left="0px" 
        top="0px" 
        backgroundColor="white"
        fontFamily="EB Garamond"
        fontSize="24px"
        paddingBottom="36px"
        boxShadow="0px 12px 12px rgba(0, 0, 0, 0.02), 0px 4px 8px rgba(0, 0, 0, 0.02)"
    >
        {this.menu.map((menuItem, index) => {
          const wordSpacing = 1;
          const pixels = menuItem.title.length * wordSpacing;

          const pathMatch = menuItem.path === this.props.path;

          return (
            <Box key={index} 
                paddingBottom="8px"
                paddingTop="8px"
            >
              <Row
                props={{ key: index }}
                justifyContent="center"
                alignItems="center"
              >
                <Box
                  position="relative"
                  props={{
                    onMouseEnter: () => this.setState({ menuItem: index }),
                    onMouseLeave: () => this.setState({ menuItem: -1 }),
                  }}
                >
                  <Link to={menuItem.path}>
                    <InlineBlock
                      color={pathMatch ? "#6B70E6" : "black"}
                      transition="letter-spacing 500ms, margin-left 500ms"
                      letterSpacing={
                        this.state.menuItem == index
                          ? wordSpacing + "px"
                          : "0px"
                      }
                      marginLeft={this.state.menuItem == index
                        ? "-" + pixels / 2 + "px" : "0px"}
                    >
                      {menuItem.title}
                    </InlineBlock>
                    <Inline
                      width={this.state.menuItem == index ? "100%" : "0%"}
                      height="2px"
                      position="absolute"
                      left={this.state.menuItem == index ? "0%" : "50%"}
                      bottom="0"
                      backgroundColor={pathMatch ? "#6B70E688" : "#44444466"}
                      transitionDuration="500ms"
                      pointerEvents="none"
                    />
                  </Link>
                </Box>
              </Row>
            </Box>
          );
        })}
      </Col>)
      }
      {!this.state.dropdownOpen ? (
      <Box 
        position="relative"
        component="img" 
        zIndex="9999"
        width="32px"
        height="32px"
        hoverCursor="pointer"
        props={{src:menu, onClick: () => this.setState({dropdownOpen: true})}} 
        alt="menu icon"
      />):( <Box 
        position="relative"
        component="img" 
        zIndex="9999"
        width="32px"
        hoverCursor="pointer"
        height="32px"
        props={{src:close, onClick: () => this.setState({dropdownOpen: false})}} 
        alt="menu icon"
        />
          )}
          </Box>
    );
  }
}

export default DropdownMenu;
